/*
 * This file contains all breakpoints used by respond() SCSS function and
 * is also converted to JSON for use in rework-pure-grids
 * NOTE: Leave comma after last item for proper JSON conversion
 *
 * Pixel sizes:
 *	xs: 320px
 *	sm:  568px
 *	md: 768px
 *	lg:  1024px
 *  xl: 1280px
 */
/*
 * A file for Custom mixins and placeholders
 */
.rating {
  text-align: center;
  width: 100%;
  padding: 35px 0 40px;
  position: relative;
  z-index: 999;
  background: #404042;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.rating .sr-only {
  display: none;
}
@media screen and (min-width: 48em) {
  .rating {
    padding: 55px 0 60px;
  }
}
@media print {
  .rating {
    display: none;
  }
}
.rating__form {
  padding: 0 20px 0;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}
@media screen and (min-width: 48em) {
  .rating__form {
    padding: 0 20px 0;
  }
}
.rating__form--hidden {
  display: none;
}
.rating__title {
  color: #fff;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  padding: 0;
  margin: 0;
}
.rating--disabled .rating__title {
  opacity: 0.6;
}
.rating__into, .rating__result, .rating__message {
  padding: 12px 0 0;
  color: #fff;
}
.rating__into p, .rating__result p, .rating__message p {
  color: #fff;
  margin-top: 0;
  font-size: 16px;
  line-height: 24px;
  font-family: "FiraSans", Arial, sans-serif;
}
.rating__into *:last-child, .rating__result *:last-child, .rating__message *:last-child {
  margin-bottom: 0;
}
.rating__into a, .rating__result a, .rating__message a {
  color: #fff;
  border-bottom: 1px dashed rgba(255, 255, 255, 0.8);
  opacity: 1;
  transition: 0.3s all;
}
.rating__into a:hover, .rating__result a:hover, .rating__message a:hover {
  opacity: 0.8;
}
.rating--disabled .rating__into, .rating--disabled .rating__result, .rating--disabled .rating__message {
  opacity: 0.6;
}
.rating__stars {
  padding: 20px 0 0;
}
.rating__stars p {
  color: #FFF;
  min-height: 1em;
  line-height: 1em;
  margin: 0;
}
.rating--disabled .rating__stars p {
  opacity: 0.5;
}
.rating__tags .MuiChip-root {
  color: #FFF;
  border-color: #FFF;
}
.rating__tags .MuiChip-root:hover {
  background-color: #FFF;
  color: #000;
}
.rating__tags .MuiChip-clickable.MuiChip-outlined:hover {
  background-color: #FFF;
  color: #000;
}
.rating__comments-outer {
  padding: 10px 0;
}
.rating__comments-field {
  margin: 0;
  padding: 6px 0;
}
.rating__comments-field label {
  display: block;
  margin: 0 0 10px;
  font-size: 13px;
  color: #fff;
}
.rating__comments-field textarea {
  border: none;
  font-size: 16px;
  color: #fff;
  padding: 5px 10px;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0.12);
  width: 100%;
  max-width: 600px;
  resize: vertical;
  font-family: "Gustan", Arial, sans-serif;
}
.rating--disabled .rating__comments-field {
  opacity: 0.6;
}
.rating__comments-toggle {
  font-size: 16px;
  line-height: 24px;
  background: none;
  border: none;
  box-shadow: none;
  color: #fff;
  opacity: 1;
  transition: all 300ms;
  padding: 6px 10px;
  font-family: "Gustan", Arial, sans-serif;
}
.rating__comments-toggle:hover, .rating__comments-toggle:active {
  opacity: 0.9;
  cursor: pointer;
}
.rating__comments-toggle:after {
  content: " ";
  display: inline-block;
  vertical-align: middle;
  width: 5px;
  height: 10px;
  margin-left: 8px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: svg-load("rating-chevron.svg", fill=#ffffff);
  background-size: cover;
  transform: rotate(90deg);
  transition: all 300ms;
}
.rating--expanded .rating__comments-toggle:after {
  transform: rotate(-90deg) !important;
}
.rating--disabled .rating__comments-toggle {
  opacity: 0.6;
}
.rating__comments {
  overflow: hidden;
  max-height: 0px;
  transition: max-height 300ms;
}
.rating__comments--expanded {
  max-height: 500px;
}
.rating__error {
  color: #fff;
  margin: 0;
  padding: 4px 0 10px;
  font-size: 14px;
  line-height: 16px;
  font-family: "FiraSans", Arial, sans-serif;
  font-style: italic;
}
.rating__loading {
  opacity: 0.6;
  padding: 12px 0 0;
}
.rating__loading .loading {
  height: 24px;
  min-height: 24px;
}
.rating__loading .MuiCircularProgress-root {
  width: 24px !important;
  height: 24px !important;
}
.rating__loading circle {
  color: #fff !important;
}
.rating .button--rating {
  margin: 12px auto 0;
  max-width: 140px;
  width: 100%;
  background-color: #CF2659;
  border: 1px solid #CF2659;
  border-radius: 3px;
  color: white;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 800;
  height: 40px;
  line-height: 1;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s, border 0.3s;
}
.rating .button--rating:hover {
  background-color: #a5004e;
  border: 1px solid #a5004e;
}
.rating .button--rating[disabled] {
  opacity: 0.6;
  background: #ccc;
  border-color: #ccc;
}
.rating .button--rating[disabled]:focus, .rating .button--rating[disabled]:hover {
  opacity: 0.6;
  background: #ccc;
  border-color: #ccc;
}