.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 95px;
  animation: loading-fadein 0.5s;
}
.modal .loading {
  min-height: 180px;
  padding: 50px 0;
}
.loading--events-showmore {
  min-height: 150px;
}
.loading--events-start {
  min-height: 300px;
}

@keyframes loading-fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}