.tag__disabled {
  pointer-events: none;
  opacity: 0.5;
}
.tag__item {
  display: inline-block;
  position: relative;
}
.tag__item input[type=checkbox] {
  opacity: 0;
  width: 1px;
  height: 1px;
  position: absolute;
}
.tag__item input[type=checkbox]:focus + label, .tag__item input[type=checkbox]:checked + label {
  background-color: #FFF;
  color: #000;
}
.tag__item label {
  position: relative;
  display: inline-block;
  padding: 0.75em;
  margin: 0 0.5em;
  color: #fff;
  border: 1px solid;
  cursor: pointer;
  transition: 0.25s ease;
}
.tag__item label:hover {
  background-color: #FFF;
  color: #000;
}